<template>
  <div class="d-main-wrap">
    <div class="h-header">
      <header>
        <figure class="h-back">
          <!--<img src="../assets/img/a7_back@3x.png" height="51" width="27" alt="back"/>-->
        </figure>
      </header>
    </div>
    <div>
      <div class="video">
        <div class="video_try_see_wrap">
          <!-- 直播播放 -->
          <div class="live-player"></div>
          <!-- 直播播放end -->
          <!-- 普通视屏播放 -->

          <video id="demo-video" class="video-js vjs-big-play-centered">
            <source :src=data.url type="video/mp4">
          </video>
          <!-- 普通视屏播放end -->
        </div>
      </div>
    </div>
    <div class="nav_wrap">
      <ul class="under-wrap">
        <li class="d-sub-nav" v-for="(item,index) in list" :key="index" @click="SelectDetail(item)">
          {{item.name}}
          <i class="d-sub-nav-i" v-bind:class="{'d-sub-nav-i-active':active===item.id}" ></i>
        </li>
      </ul>
    </div>
    <div>
      <comment :datalist="datalist" v-if="active==1" :show='false'></comment>
      <div  v-if="active==2" class="content" v-html="data.describe">{{data.describe}}
      </div>
    </div>
  </div>
</template>

<script>
import comment from "../subViews/comment";
import api from "@/api/share";
export default {
  name: "lessoningDetails",
  components: { comment },
  data() {
    return {
      teacher: {},
      datalist: {},
      active: "1",
      content: "",
      data: {},
      list: [
        {
          name: "评论",
          id: "1"
        },
        {
          name: "文档",
          id: "2"
        }
      ]
    };
  },
  created() {
    console.log("===>1");
    this.Getdatalist();
    this.Getcoursedetails();
  },
  methods: {
    SelectDetail(item) {
      this.active = item.id;
    },
    Getdatalist() {
      let query = this.$route.query;
      console.log("url参数=>", query);
      api
        .datalist({
          id: query.id,
          type: query.type,
          type_id: query.type_id
        })
        .then(res => {
          console.log("详情=>", res);
          if (res.status == 1) {
            this.datalist = res.data.data;
          } else this.$toast(res.msg);
        });
    },
    Getcoursedetails() {
      api
        .coursedetails({
          id: this.$route.query.id
        })
        .then(res => {
          console.log("课程详情=>", res);
          if (res.status == 1) {
            this.data = res.data;
          } else this.$toast(res.msg);
        });
    }
  }
};
</script>

<style scoped>
.content {
  padding: 20px;
}
.d-main-wrap {
  display: inline-block;
  width: 100%;
  height: auto;
}

.h-header {
  position: fixed;
  width: 100%;
  height: 0.45rem;
  top: 0;
  left: 0;
  z-index: 999;
  background: none;
}

.h-header header {
  position: relative;
  width: 94%;
  height: 0.45rem;
  margin: 0 auto;
}

.h-back {
  position: absolute;
  width: 0.22rem;
  height: auto;
  top: 28%;
  left: 0;
  z-index: 2;
}

.h-back-r {
  position: absolute;
  width: 0.22rem;
  height: auto;
  top: 28%;
  right: 0;
  z-index: 2;
}

.h-back > img,
.h-back-r > img {
  width: 0.1rem;
  height: auto;
}

.video {
  width: 100%;
  height: 2.1rem;
  background: #000;
  position: relative;
}

.video_try_see_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 222;
  color: #fff;
}

.nav_wrap {
  width: 90%;
  height: auto;
  margin: 0.12rem auto;
}
.under-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: auto;
  overflow-x: scroll;
  white-space: nowrap;
}
.under-wrap li {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: auto;
  text-align: center;
  font-size: 0.14rem;
  padding: 0 0.2rem;
}
.d-sub-nav-active {
  color: #f44336;
  font-weight: 600;
}
.d-sub-nav-i-active {
  border-bottom: 0.02rem solid #f44336;
}
.d-nav-i,
.d-nav-i-active,
.d-sub-nav-i,
.d-sub-nav-i-active {
  display: block;
  width: 34%;
  height: 0.01rem;
  margin: 0.04rem auto;
}
</style>



