<template>
  <div class="openClassDetails" v-if="data.id">
    <div class="openClass_notice_box">
      <p class="openClass_title_box">{{data.name}}</p>
      <p class="openClass_tiem_box"><span>{{data.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{data.author}}</span></p>
      <div class="openClass_content">
        <p v-html="data.contents"> {{data.contents}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import comment from "../subViews/comment";
import api from "@/api/share";
export default {
  name: "openClassDetails",
  components: { comment, api },
  data() {
    return {
      data: {},
      datalist: {},
      content: ""
    };
  },
  created() {
    this.GetarticleDetail();
  },
  methods: {
    GetarticleDetail() {
      api
        .introducedetails({
          id: this.$route.query.id
        })
        .then(res => {
          console.log(res);
          if (res.status == 1) {
            this.data = res.data;
          } else this.$toast(res.msg);
        });
    }
  }
};
</script>
<style>
.openClass_notice_box {
  padding: 10px;
}
.openClass_title_box {
  font-size: 0.18rem;
  color: #333333;
}
.openClass_tiem_box {
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}
.openClass_tiem_box span:last-child {
  float: right;
}
.openClass_content {
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
  white-space: nowrap;
}
.openClassDetails .openClass_content img {
  width: 100%;
  object-fit: cover;
}
.openClass_notice_box .openClass_content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
}
</style>



