<template>
  <div id="inform_content" v-if="data.id">
    <div class="inform_notice_box">
      <p class="inform_title_box">{{data.name}}</p>
      <p class="inform_tiem_box"><span>{{data.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{data.author}}</span></p>
      <div class="inform_content" v-html='data.contents'>
      </div>
    </div>
  </div>

</template>

<script>
const u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; //android终端
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
import comment from "../subViews/comment";
import api from "@/api/share";
export default {
  name: "mobileInformationDetails",
  components: { comment },
  data() {
    return {
      data: {},
      datalist: {},
      content: ""
    };
  },
  mounted() {
    this.GetarticleDetail();
  },
  methods: {
    Getimg(event) {
      if (event.target.tagName == "IMG") {
        if (isAndroid) {
          android.showImg(event.target.currentSrc);
        } else if (isiOS) {
          window.location.href = "image-preview:" + event.target.currentSrc;
        }
      }
    },
    GetarticleDetail() {
      api
        .articleDetail({
          id: this.$route.query.id
        })
        .then(res => {
          console.log("详情返回=>", res);
          if (res.status == 1) {
            this.data = res.data;
            this.$nextTick(() => {
              document
                .getElementsByClassName("inform_content")[0]
                .addEventListener("click", this.Getimg);
            });
          } else this.$toast(res.msg);
        });
    }
  },
  destroyed: function() {
    document
      .getElementsByClassName("inform_content")[0]
      .removeEventListener("click", this.Getimg);
  }
};
</script>
<style >
.inform_notice_box {
  padding: 10px;
}
.inform_title_box {
  font-size: 0.18rem;
  color: #333333;
}
.inform_tiem_box {
  width: 100%;
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}
.inform_tiem_box span:last-child {
  float: right;
}
#inform_content .inform_content {
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
  text-indent: 2em;
  word-wrap: break-word;
}
#inform_content .inform_content img {
  width: 100%;
  object-fit: cover;
}
#inform_content .inform_content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
}
</style>



