<template>
  <div id="ResearchDetail">
    <div class="notice_box">
      <p class="title_box">{{article.name}}</p>
      <p class="tiem_boxs"><span>{{article.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{article.author}}</span></p>
      <div class="content" v-html="article.contents"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ResearchDetail",
  props: {
    article: {
      type: Object,
      default: ""
    }
  }
};
</script>
<style scoped>

.footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 0.49rem;
  line-height: 0.49rem;
  text-align: center;
  background-color: white;
}

.footer p:last-child {
  float: right;
  background-color: #e80414;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-bottom: 0.045rem;
  font-size: 0.14rem;
  padding: 0 20px;
  border-radius: 3px;
  color: white;
  margin-right: 0.1rem;
}
.footer input {
  background: #f1f1f1;
  float: left;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-bottom: 0.05rem;
  padding: 0 10px;
  font-size: 0.14rem;
  width: 65%;
  margin-left: 10px;
}
.notice_box {
  padding: 10px;
}
.top_bg {
  width: 100%;
  height: 1.9rem;
  object-fit: cover;
}
.title_box {
  font-size: 0.18rem;
  color: #333333;
}
.tiem_boxs {
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}
.tiem_boxs span:last-child {
  float: right;
}

#ResearchDetail .content {
  width: 100%;
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
  
}
#ResearchDetail .content img {
  width: 100%;
  height: 1.9rem;
  object-fit: cover;
}
/* #ResearchDetail .content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
} */
</style>



