import Vue from 'vue'
import Router from 'vue-router'

import InformationDetails from '@/components/mobile/mobileInformationDetails'//咨询详情b65
import notiveDetails1 from '@/components/mobile/mobileNotiveDetails'//通知详情b29
import hotSpotDetails from '@/components/mobile/mobileHotSpotDetails'//热点详情b38==b63
import mobileResearchDetail from '@/components/mobile/mobileResearchDetail'//热点详情b65==c8

import learnClass from '@/components/share/learnClass' //学习班详情b5
import NotiveDetails from '@/components/share/NotiveDetails'//通知详情b29
import hotSpot from '@/components/share/hotSpot'//分享热点详情b38==b63
import ResearchDetail from '@/components/share/ResearchDetail'//研究动态详情b65==c8
import openClassDetails from '@/components/share/openClassDetails'//公开课详情C6==e5==E9
import articleDetails from '@/components/share/articleDetails'//详情C22
import lessoningDetails from '@/components/share/lessoningDetails'//直播课详情-付费的C27
// import InformDetails from '@/components/share/InformDetails'//直播课详情-付费的C27

//组件
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/components/mobile/mobileResearchDetail',
      name: 'mobileResearchDetail',
      meta: {
        title: '研究动态详情',
      },
      component: mobileResearchDetail
    }, {
      path: '/components/mobile/mobileInformationDetails',
      name: 'mobileInformationDetails',
      meta: {
        title: '咨询详情',
      },
      component: InformationDetails
    },
    {
      path: '/components/mobile/mobileHotSpotDetails',
      name: 'mobileHotSpotDetails',
      meta: {
        title: '热点详情',
      },
      component: hotSpotDetails
    },
    {
      path: '/components/mobile/mobileNotiveDetails',
      name: 'mobileNotiveDetails',
      meta: {
        title: '通知详情',
      },
      component: notiveDetails1
    },
    {
      path: '/components/share/lessoningDetails',
      name: 'lessoningDetails',
      meta: {
        title: '直播课详情',
      },
      component: lessoningDetails
    }, {
      path: '/components/share/NotiveDetails',
      name: 'NotiveDetails',
      meta: {
        title: '通知详情',
      },
      component: NotiveDetails
    },
    {
      path: '/components/share/openClassDetails',
      name: 'openClassDetails',
      meta: {
        title: '公开课详情',
      },
      component: openClassDetails
    },
    {
      path: '/components/share/ResearchDetail',
      name: 'ResearchDetail',
      meta: {
        title: '研究动态详情',
      },
      component: ResearchDetail
    },
    {
      path: '/components/share/learnClass',
      name: 'learnClass',
      meta: {
        title: '学习班详情',
      },
      component: learnClass
    },
    {
      path: '/components/share/hotSpot',
      name: 'hotSpot',
      meta: {
        title: '热点详情',
      },
      component: hotSpot
    }, {
      path: '/components/share/articleDetails',
      name: 'articleDetails',
      meta: {
        title: '详情',
      },
      component: articleDetails
    }
    // {
    //   path: '/components/share/InformDetails',
    //   name: 'InformDetails',
    //   meta: {
    //     title: '详情',
    //   },
    //   component: InformDetails
    // }
  ]
})



