<template>
  <div id="ResearchDetail" v-if="article.id">
    <artice-detail :article="article"></artice-detail>
    <div style="background-color: #fafafa;height: 10px"></div>
    <comment :datalist="datalist" :show='true'></comment>
  </div>
</template>

<script>
import NoticeDetail from "../subViews/NoticeDetail";
import articeDetail from "../subViews/articeDetail";
import comment from "../subViews/comment";
import api from "@/api/share";
export default {
  name: "ResearchDetail",
  components: { comment, NoticeDetail, articeDetail },
  data() {
    return {
      article: { teacher: { name: "" } },
      datalist: { name: "" }
    };
  },
  created() {
    this.Gethotdetails();
    this.Getdatalist();
  },
  methods: {
    Gethotdetails() {
      api
        .articleDetail({
          id: this.$route.query.id
        })
        .then(res => {
          console.log(res);
          if (res.status == 1) {
            this.article = res.data;
          } else this.$toast(res.msg);
        });
    },
    Getdatalist() {
      let data = this.$route.query;
      api
        .datalist({
          id: data.id,
          type: data.type,
          type_id: data.type_id
        })
        .then(res => {
          if (res.status == 1) {
            this.datalist = res.data.data;
          } else this.$toast(res.msg);
        });
    }
  }
};
</script>
<style scoped>
.footer p:last-child {
  float: right;
  background-color: #e80414;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-bottom: 0.045rem;
  font-size: 0.14rem;
  padding: 0 20px;
  border-radius: 3px;
  color: white;
  margin-right: 0.1rem;
}
.footer input {
  background: #f1f1f1;
  float: left;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-bottom: 0.05rem;
  padding: 0 10px;
  font-size: 0.14rem;
  width: 65%;
  margin-left: 10px;
}
.notice_box {
  padding: 10px;
}
.top_bg {
  width: 100%;
  height: 1.9rem;
  object-fit: cover;
}
.title_box {
  font-size: 0.18rem;
  color: #333333;
}
.tiem_boxs {
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}
.tiem_boxs span:last-child {
  float: right;
}
.content {
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
}
</style>



