<template>
  <div id="NotiveDetails_box" v-if="notice.id">
    <NoticeDetail :notice="notice"></NoticeDetail>
    <div style="background-color: #fafafa;height: 10px"></div>
    <user :user="user"></user>
  </div>
</template>
<script>
import NoticeDetail from "../subViews/NoticeDetail";
import user from "../subViews/user";
import api from "@/api/share";
export default {
  name: "NotiveDetails",
  components: { NoticeDetail, user },
  data() {
    return {
      notice: {},
      user: {}
    };
  },
  created() {
    this.GetnoticeDetail();
  },
  methods: {
    GetnoticeDetail() {
      api
        .noticeDetail({
          id: this.$route.query.id
        })
        .then(res => {
          console.log("=============", res);
          if (res.status == 1) {
            this.notice = res.data.notice;
            this.user = res.data.user.length <= 0 ? {} : res.data.user;
            console.log("notice", this.notice);
            console.log("user", this.user);
          } else this.$toast(res.msg);
        });
    }
  }
};
</script>

<style >
#NotiveDetails_box .content img {
  width: 100%;
  object-fit: cover;
}
#NotiveDetails_box .content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
}
</style>




