<template>
  <div id="ResearchDetail">
    <artice-detail :article="article"></artice-detail>
  </div>
</template>
<script>
  const u = navigator.userAgent;
  var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
  var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
  import  NoticeDetail from '../subViews/NoticeDetail'
  import  articeDetail from '../subViews/articeDetail'
  import  comment from '../subViews/comment'
  import api from '@/api/share'
  export default {
    name: 'ResearchDetail',
    components:{comment,api,NoticeDetail,articeDetail},
    data () {
      return {
        teacher:{teacher:{name:""}},
        article:{name:""}
      }
    },
    mounted(){
      this.Gethotdetails()
    },
    methods: {
      Getimg(event){
        if (event.target.tagName=='IMG'){
          if(isAndroid){android.showImg(event.target.currentSrc);}
          else  if(isiOS){
            window.location.href='image-preview:'+event.target.currentSrc;
          }
        }
      },
      Gethotdetails() {
        api.articleDetail({
        }).then(res => {
          console.log(res)
          if (res.status == 1) {
            this.article=res.data;
            document.getElementsByClassName("content")[0].addEventListener('click',this.Getimg)
          } else
            this.$toast(res.msg)
        })
      },
      destroyed:function () {
        document.getElementsByClassName("content")[0].removeEventListener('click',this.Getimg)
      }
    }
  }
</script>



