<template>
  <div id="hotSpot">
    <div class="notice_box">
      <p class="title_box">{{teacher.name}}</p>
      <p class="tiem_box"><span>{{teacher.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{teacher.teacher.name}}</span></p>
      <div class="content"  v-html=teacher.contents></div>
      <div style="background-color: #fafafa;height: 10px"></div>
        <comment :datalist="datalist" :show='true'></comment>
    </div>
  </div>
</template>

<script>
import comment from "../subViews/comment";
import api from "@/api/share";
export default {
  name: "hotSpotDetails",
  components: { comment },
  data() {
    return {
      teacher: { name: "", teacher: { name: "" } },
      datalist: {}
    };
  },
  created() {
    this.Gethotdetails();
    this.Getdatalist();
  },
  methods: {
    Gethotdetails() {
      api
        .hotdetails({
          id: this.$route.query.id
        })
        .then(res => {
          console.log(res);
          if (res.status == 1) {
            this.teacher = res.data;
          } else this.$toast(res.msg);
        });
    },
    Getdatalist() {
      let data = this.$route.query;
      api
        .datalist({
          id: data.id,
          type: data.type,
          type_id: data.type_id
        })
        .then(res => {
         
          if (res.status == 1) {
            this.datalist = res.data.data;
             console.log('详情====>',this.datalist);
          } else this.$toast(res.msg);
        });
    }
  }
};
</script>
<style >
.footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 0.49rem;
  line-height: 0.49rem;
  text-align: center;
  background-color: white;
}
.footer p:last-child {
  float: right;
  background-color: #e80414;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-bottom: 0.045rem;
  font-size: 0.14rem;
  padding: 0 20px;
  border-radius: 3px;
  color: white;
  margin-right: 0.1rem;
}
.footer input {
  background: #f1f1f1;
  float: left;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-bottom: 0.05rem;
  padding: 0 10px;
  font-size: 0.14rem;
  width: 65%;
  margin-left: 10px;
}
.notice_box {
  padding: 10px;
}
.top_bg {
  width: 100%;
  height: 1.9rem;
  object-fit: cover;
}
.title_box {
  font-size: 0.18rem;
  color: #333333;
}
.tiem_box {
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}
.tiem_box span:last-child {
  float: right;
}
#hotSpot .content {
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
  width: 100%;
}
#hotSpot .content img {
  width: 100%;
  object-fit: cover;
}
#hotSpot .content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
}
</style>



