<template>
  <div id="hot_content">
    <div class="hot_notice_box">
      <p class="hot_title_box">{{teacher.name}}</p>
      <p class="hot_tiem_box"><span>{{teacher.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{teacher.teacher.name}}</span>
      </p>
      <div class="hot_content" v-html='teacher.contents'></div>
    </div>
  </div>
</template>

<script>
const u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; //android终端
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
import comment from "../subViews/comment";
import api from "@/api/share";

export default {
  name: "mobileHotSpotDetails",
  // components:{comment,api},
  components: { comment },
  data() {
    return {
      teacher: { name: "", contents: "", add_time: "", teacher: { name: "" } },
      datalist: {}
    };
  },
  mounted() {
    this.Gethotdetails();
  },
  methods: {
    Getimg(event) {
      if (event.target.tagName == "IMG") {
        debugger;
        if (isAndroid) {
          android.showImg(event.target.currentSrc);
        } else if (isiOS) {
          window.location.href = "image-preview:" + event.target.currentSrc;
        }
      }
    },
    Gethotdetails() {
      api
        .hotdetails({
          id: this.$route.query.id
        })
        .then(res => {
          console.log(res);
          if (res.status == 1) {
            this.teacher = res.data;
            document
              .getElementsByClassName("hot_content")[0]
              .addEventListener("click", this.Getimg);
          } else this.$toast(res.msg);
        });
    }
  },
  destroyed: function() {
    this.$nextTick(() => {
      document
        .getElementsByClassName("hot_content")[0]
        .removeEventListener("click", this.Getimg);
    });
  }
};
</script>
<style>
.hot_notice_box {
  padding: 10px;
}

.hot_title_box {
  font-size: 0.18rem;
  color: #333333;
}

.hot_tiem_box {
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}

.hot_tiem_box span:last-child {
  float: right;
}

.hot_content {
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
  width: 100%;
  word-wrap: break-word;
}

#hot_content .hot_content img {
  width: 100%;
  object-fit: cover;
}

#hot_content .hot_content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
}
</style>



