<template>
  <div id="notive_content">
    <div class="notive_notice_box">
      <p class="notive_title_box">{{teacher.title}}</p>
      <p class="notive_tiem_box"><span>{{teacher.created_at|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{teacher.teacher_name}}</span></p>
      <div class="notive_content" v-html='teacher.content'></div>
    </div>
  </div>
</template>

<script>
const u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; //android终端
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
import comment from "../subViews/comment";
import api from "@/api/share";
export default {
  name: "mobileNotiveDetails",
  components: { comment },
  data() {
    return {
      teacher: {},
      datalist: {}
    };
  },
  mounted() {
    this.Gethotdetails();
  },

  methods: {
    Getimg(event) {
      if (event.target.tagName == "IMG") {
        if (isAndroid) {
          android.showImg(event.target.currentSrc);
        } else if (isiOS) {
          window.location.href = "image-preview:" + event.target.currentSrc;
        }
      }
    },
    Gethotdetails() {
      api.noticeDetail({
        id:this.$route.query.id
      }).then(res => {
        console.log(res);
        if (res.status == 1) {
          this.teacher = res.data.notice;
          document
            .getElementsByClassName("notive_content")[0]
            .addEventListener("click", this.Getimg);
        } else this.$toast(res.msg);
      });
    }
  },
  destroyed: function() {
    document
      .getElementsByClassName("notive_content")[0]
      .removeEventListener("click", this.Getimg);
  }
};
</script>
<style >
.notive_notice_box {
  padding: 10px;
}
.notive_title_box {
  font-size: 0.18rem;
  color: #333333;
}
.notive_tiem_box {
  font-size: 0.12rem;
  color: #999999;
  margin: 10px 0;
}
.notive_tiem_box span:last-child {
  float: right;
}
#notive_content .notive_content {
  font-size: 0.15rem;
  color: #666666;
  letter-spacing: 0;
  line-height: 0.3rem;
  width: 100%;
  word-wrap: break-word;
}
#notive_content .notive_content img {
  width: 100%;
  object-fit: cover;
}
#notive_content .notive_content video {
  width: 100%;
  height: 75%;
  margin: 10px 0;
}
</style>



